.skills-container {
  max-width: 960px;
}

.bars-container {
  max-width: 850px;
  font-size: 14px;
  background-color: #fff;
  box-shadow: 0 1px 6px rgba(0,0,0,.12), 0 1px 4px rgba(0,0,0,.24);
  padding: 40px 50px;
}

.skill-progress-bar {
  height: 5px;
}

.skill-name-bar {
  margin-bottom: 40px;
}

.skill-name-bar:last-child {
  margin-bottom: 0;
}

.skill-image img {
  margin: auto auto;
}

.technologies-container{
  background-color: #fff;
  box-shadow: 0 1px 6px rgba(0,0,0,.12), 0 1px 4px rgba(0,0,0,.24);
  padding: 30px 0px;
}

@media (max-width: 768px){
  .skill-image img {
    height: 45px
  }

  .timeline li .timeline-panel {
    padding: 20px 2%;
  }

  .bars-container {
    padding: 40px 10px;
  }
}
