.about-container {
  background-color: #fff;
  max-width: 960px;
  box-shadow: 0 1px 6px rgba(0,0,0,.12), 0 1px 4px rgba(0,0,0,.24);
  padding-left: 0;
  padding-right: 0;
  position: relative;
  z-index: 2;
}

.profile-position {
  font-size: 18px;
  font-weight: 400;
  line-height: 1.1;
  margin-bottom: 0;
}

.profile {
  padding: 57px 50px 15px;
}

.profile-photo {
  margin-right: 10%;
  margin-bottom: 10px;
}

.profile-photo img {
  max-height: 100%;
  max-width: 100%;
}

@media (max-width: 992px) {
  .profile-photo {
    margin-right: 0;
    margin-bottom: 30px;
  }
}

.profile .profile-details-section{
  max-width: 400px;
  margin: auto;
}

.profile .profile-item-name {
  width: 120px;
  color: #333;
  font-size: 12px;
  font-weight: 700;
  line-height: 20px;
  text-transform: uppercase;
}

.profile .content {
  font-size: 15px;
  font-weight: 400;
  line-height: 20px;
  color: #9da0a7;
}

.profile-item {
  margin-bottom: 13px;
}

.profile-social{
  background-color: #07cb79;
  padding: 15px 0;
}

.social-bar {
  margin: 0;
  padding: 0;
  list-style: none;
  text-align: center;
}

.social-bar li {
  display: inline-block;
  margin: 5px 15px;
}

.social-bar li a {
  line-height: 45px;
  width: 45px;
  height: 45px;
  position: relative;
  display: inline-block;
  background-color: transparent;
  -webkit-transition: -webkit-transition,background-color .25s linear 0s;
  -moz-transition: -moz-transition,background-color .25s linear 0s;
  transition: transition,background-color .25s linear 0s;
  -webkit-backface-visibility: hidden;
  -moz-backface-visibility: hidden;
  backface-visibility: hidden;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  border-radius: 50%;
}

.social-bar li a:hover {
  text-decoration: none;
  background-color: rgba(0,0,0,.1);
}

.social-bar li a i {
  color: #fff;
  font-size: 20px;
  line-height: 45px;
  content: "\f099";
  margin-right: 0;
}

.summary-container {
  color: #000;
  font-size: 20px;
  font-weight: 300;
  font-style: normal;
  line-height: 1.8;
  text-align: center;
  margin-top: 30px;
  padding-left: 5%;
  padding-right: 5%;
  max-width: 960px;
}

.summary-container .btn {
  padding: 21px 55px;
  letter-spacing: .05em;
  margin: 5px;
}

.ripple {
  overflow: hidden;
  position: relative;
  user-select: none;
  display: inline-block;
  -webkit-backface-visibility: hidden;
  -moz-backface-visibility: hidden;
  backface-visibility: hidden;
  -webkit-transform: translate3d(0,0,0);
  -moz-transform: translate3d(0,0,0);
  transform: translate3d(0,0,0);
}

.btn-border {
  font-size: 14px;
  font-weight: 700;
  line-height: 1;
  text-transform: uppercase;
  color: #3d4451!important;
  border: 1px solid #cbcdcf;
  background: 0 0;
  box-shadow: none;
}

.ripple:hover{
  opacity: .95;
  text-decoration: none;
  box-shadow: 0 5px 11px 0 rgba(0,0,0,.18), 0 4px 15px 0 rgba(0,0,0,.15);
}

.ripple.is-hover{
  opacity: .95;
  text-decoration: none;
  box-shadow: 0 5px 11px 0 rgba(0,0,0,.18), 0 4px 15px 0 rgba(0,0,0,.15);
}



.head-bg {
  background-image: url("/assets/wood.jpg");
  top: 0;
  left: 0;
  right: 0;
  z-index: 1;
  display: block;
  position: absolute;
  background-size: cover;
  background-color: #242832;
  background-repeat: no-repeat;
  background-position: center;
  height: 515px;
}

.head-bg:before {
  background-color: rgba(44, 51, 64, 0.8);
}
.head-bg:before {
  content: '';
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  position: absolute;
}

.align-text-centre{
 text-align: center;
}


@media (max-width: 768px){
  .profile h1{
    font-size: 2.0rem;
  }
  .social-bar li {
    margin: 5px 13px;
  }
  .head-bg {
    display: none;
  }

}
