.top-container {
  position: fixed;
  box-shadow: 0 10px 30px rgba(0,0,0,.19), 0 6px 10px rgba(0,0,0,.23);
  background-color: #fff;
  z-index: 3;
  padding-left: 0;
  padding-right: 0;
}

.navbar .nav-item {
  position: relative;
}

.navbar .nav-item::after {
  content: '';
  width: 0;
}

.navbar .nav-item.selected:after, .navbar .nav-item:hover:after {
  width: 100% !important;
}

.navbar .nav-item:after{
  width: 0;
  content: "";
  background-color: #007bff;
  position: absolute;
  left: 0;
  bottom: 0;
  height: 3px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  border-radius: 10px;
  -webkit-transition: width .2s ease-out;
  -moz-transition: width .2s ease-out;
  transition: width .2s ease-out;
}

.top-name {
  font-size: 20px;
  line-height: 1;
  margin-bottom: 3px;
  font-family: 'Oswald';
  font-weight: 400;
}

.top-title {
  color: #9a9da2;
  font-size: 15px;
  line-height:1.1;
  margin: 0;
  font-family: 'Gentium Book Basic';
  font-style: normal;
}

/*
  Tooltip
*/

.tooltip-button {
  background-color: #07cb79;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  border-radius: 3px;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
  -webkit-transition: all .3s ease-out;
  -moz-transition: all .3s ease-out;
  transition: all .3s ease-out;
  color: #fff;
  font-size: 14px;
  font-weight: 700;
  text-transform: uppercase;
  padding: 10px 20px;
  min-width: 130px;
  margin-bottom: 10px;
}

.tooltip{
  opacity: 1 !important;

}

.tooltip-inner{
  background-color: white;
  box-shadow: 0 10px 30px rgba(0,0,0,.19), 0 6px 10px rgba(0,0,0,.23);
}

.navbar-toggler{
  padding: 10px 0;
}

.navbar-toggle {
  background-color: #07cb79;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  border-radius: 3px;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
  -webkit-transition: all .3s ease-out;
  -moz-transition: all .3s ease-out;
  transition: all .3s ease-out;
  color: #fff;
  font-size: 14px;
  font-weight: 700;
  text-transform: uppercase;
  min-width: 100px;
}

