.connect-container{
  max-width: 960px;
}

.contact-form-box{
  background-color: #fff;
  box-shadow: 0 1px 6px rgba(0,0,0,.12), 0 1px 4px rgba(0,0,0,.24);
  padding: 40px 50px;
  position: relative;
  border: 0;
  border-radius: 0;
}

.contact-info-box{
  position: relative;
  padding-bottom: 340px;
  min-height: 531px;
  background-color: #fff;
  box-shadow: 0 1px 6px rgba(0,0,0,.12), 0 1px 4px rgba(0,0,0,.24);
}

.contact-info-list {
  padding: 45px 35px 5px;
  margin: 0;
}

.contact-map{
  width: 100%;
  bottom: 0;
  left: 0;
}

.input-container label {
  top: 10px;
  left: 0;
  color: #333;
  font-size: 14px;
  line-height: 14px;
  font-weight: 400;
  text-transform: uppercase;
  position: absolute;
  pointer-events: none;
  -webkit-transition: .15s ease all;
  -moz-transition: .15s ease all;
  transition: .15s ease all;
}

.input-container textarea{
  font-size: 16px;
  line-height: 16px;
  padding: 10px 0;
  display: block;
  width: 100%;
  border: none;
  border-radius: 0;
  resize: none;
}

.connect-container .send-button {
  background-color: #07cb79;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  border-radius: 3px;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
  -webkit-transition: all .3s ease-out;
  -moz-transition: all .3s ease-out;
  transition: all .3s ease-out;
  color: #fff;
  font-size: 14px;
  font-weight: 700;
  text-transform: uppercase;
  padding: 10px 20px 10px 20px;
  min-width: 130px;
  margin-top: 15px;
  margin-bottom: 65px;
}

.contact-profile-item .contact-profile-item-name {
  width: 120px;
  color: #333;
  font-size: 12px;
  font-weight: 700;
  line-height: 20px;
  text-transform: uppercase;
}

.contact-profile-item .content {
  font-size: 15px;
  font-weight: 400;
  line-height: 20px;
  color: #9da0a7;
}

.contact-profile-item {
  margin-bottom: 13px;
}

.connect-social {
  padding-top: 50px;
}

.connect-social i{
  color: #d7d7d7 !important;
}

/*
  Form css
*/

.card .input-container {
  position: relative;
  margin-bottom: 30px;
}
.card .input-container input, .card .input-container textarea {
  outline: none;
  z-index: 1;
  position: relative;
  background: none;
  width: 100%;
  height: 45px;
  border: 0;
  color: black;
  font-weight: 400;
}
.card .input-container input:focus ~ label, .card .input-container textarea:focus ~ label {
  color: #9d9d9d;
  -webkit-transform: translate(-12%, -50%) scale(0.75);
  transform: translate(-12%, -60%) scale(0.75);
}
.card .input-container input:focus ~ .bar:before, .card .input-container input:focus ~ .bar:after {
  width: 50%;
}
.card .input-container textarea:focus ~ .bar:before, .card .input-container textarea:focus ~ .bar:after {
  width: 50%;
}
.card .input-container input:valid ~ label {
  color: #9d9d9d;
  -webkit-transform: translate(-12%, -50%) scale(0.75);
  transform: translate(-12%, -50%) scale(0.75);
}
.card .input-container textarea:valid ~ label {
  color: #9d9d9d;
  -webkit-transform: translate(-12%, -50%) scale(0.75);
  transform: translate(-12%, -50%) scale(0.75);
}
.card .input-container label {
  position: absolute;
  top: 0;
  left: 0;
  line-height: 60px;
  -webkit-transition: 0.2s ease;
  transition: 0.2s ease;
}
.card .input-container .bar {
  position: absolute;
  left: 0;
  bottom: 0;
  background: #757575;
  width: 100%;
  height: 1px;
}
.card .input-container .bar:before, .card .input-container .bar:after {
  content: '';
  position: absolute;
  background: #ec2652;
  width: 0;
  height: 2px;
  -webkit-transition: .2s ease;
  transition: .2s ease;
}
.card .input-container .bar:before {
  left: 50%;
}
.card .input-container .bar:after {
  right: 50%;
}

.mobile-captcha {
  margin-left: -20px;
}

